/* ------------------------------------------------------------ *\
	# (max-width: 1200px)
\* ------------------------------------------------------------ */
@media (max-width: 1200px) {
	.wrapper { width: auto; padding: 0 15px; }

	.box-services { width: 50%; }

	.header .btn-menu { display: block; }
	.nav-primary { position: absolute; top: 100%; right: 0; background: var(--color-neutral-lt); transform: translate3d(100%, 0, 0); transition: all 0.3s; }
	.nav-primary.has-flag { transform: translate3d(0, 0, 0); }
	.nav-primary ul > li { display: block; }
}
/* ------------------------------------------------------------ *\
	# (max-width: 767px)
\* ------------------------------------------------------------ */
@media (max-width: 767px) {
	.box-services-preview .figure,
	.box-services .figure { width: 80px; height: 80px; }
	.box-services-preview .figure { margin: -40px 0 0 -40px; }

	.accent-title { text-align: center; }
	.accent-services-preview .accent-head .figure { display: none; }
	.accent-services-preview .accent-title { float: none; }
	.accent-about { text-align: center; }
}

/* ------------------------------------------------------------ *\
	# (max-width: 600px)
\* ------------------------------------------------------------ */
@media (max-width: 600px) {
	.box-services { display: block; width: auto; }
	.box-services .rich-text { text-align: center; }
}